import { CssBaseline } from "@mui/material";
import { ThemeProvider, createTheme } from "@mui/material/styles";
import React from "react";
import { useSelector } from "react-redux";
// Qui stiamo importando la libreria da Material-UI per la creazione del tema

const MuiThemeProvider = ({ children }) => {
  // definiamo il componente Provider con dentro il children per poter mettere componenti all'interno
  const darkMode = useSelector((state) => state.theme);

  // e infine il dispatch per inviare tutto a redux
  const theme = createTheme({
    palette: {
      mode: darkMode,
    },
  });

  return (
    <ThemeProvider theme={theme}>
      {" "}
      <CssBaseline />
      {children}
    </ThemeProvider>
  );
};
export default MuiThemeProvider;
