import React from "react";
import {
  AppBar,
  IconButton,
  Link,
  Toolbar,
  Typography,
  Avatar,
  TextField,
  Button,
  Container,
} from "@mui/material";
import Brightness4Icon from "@mui/icons-material/Brightness4";
import Brightness7Icon from "@mui/icons-material/Brightness7";
import { toggleTheme } from "../redux/themeDarkLight";
import { useDispatch, useSelector } from "react-redux";
import MenuIcon from "@mui/icons-material/Menu";
import SearchIcon from "@mui/icons-material/Search";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import List from "@mui/material/List";
import Divider from "@mui/material/Divider";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";

import Popover from "@mui/material/Popover";
import Mylogo from "../Mylogo.png";
import LocationOnIcon from "@mui/icons-material/LocationOn";
import PersonPinIcon from "@mui/icons-material/PersonPin";

const Header = () => {
  const [open, setOpen] = React.useState(false);
  const [searchAnchor, setSearchAnchor] = React.useState(null);
  //   qui ho creato due variabili dove searchAncor contiene lo stato corrente che è null
  //   setSerchAnchor ci permette di aggiornare SearchAnchor
  //  useState restituisce un array con l'elemento delle stato iniziale e il secondo che aggiorna lo stato
  const [loginAnchor, setLoginAnchor] = React.useState(null);
  const darkMode = useSelector((state) => state.theme);
  // Con use Selector otteniamo il valore dallo stato
  const dispatch = useDispatch();

  const toggleDrawer = (newOpen) => () => {
    setOpen(newOpen);
  };

  const handleSearchClick = (event) => {
    setSearchAnchor(event.currentTarget);
    // questa funzione si chiama quando viene cliccato sull'icona e event sono le informazioni quando si clicca
    // setSearchAnchor per aggiornare searchAnchor
  };

  const handleLoginClick = (event) => {
    setLoginAnchor(event.currentTarget);
  };

  const handleSearchClose = () => {
    setSearchAnchor(null);
    // con questa funzione chiudiamo Popover quando si riclicca
  };

  const handleLoginClose = () => {
    setLoginAnchor(null);
  };
  const listItem = ["Chi siamo", "Dove"];
  const DrawerList = (
    <Box sx={{ width: 250 }} role="presentation" onClick={toggleDrawer(false)}>
      <List>
        <ListItem key={"Giovanni Sampognaro"} disablePadding>
          <ListItemButton>
            <ListItemIcon>
              <Avatar alt="GS" src="Components/Giovanni.jpg" />
            </ListItemIcon>
            <ListItemText primary={"Giovanni Sampognaro"} />
          </ListItemButton>
        </ListItem>
      </List>
      <Divider />
      <List>
        {listItem.map((text, index) => (
          <ListItem key={text} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                {index % 2 === 0 ? <PersonPinIcon /> : <LocationOnIcon />}
              </ListItemIcon>
              <ListItemText primary={text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  );
  return (
    <AppBar
      position="static"
      sx={{ backgroundColor: "#F0F0F0 ", color: "black" }}
    >
      <Container maxWidth={"xl"}>
        <Toolbar disableGutters>
          <IconButton onClick={toggleDrawer(true)}>
            <MenuIcon sx={{ color: "black" }} />
          </IconButton>
          <img
            src={Mylogo}
            alt="Logo"
            style={{ height: 28, marginRight: 17, marginLeft: 8 }}
          />

          <Link color="black" href="/" underline="hover">
            TechBazar
          </Link>

          <Box
            sx={{
              display: "flex",
              alignItems: "center",
              flexGrow: 1,
              justifyContent: "flex-end",
            }}
          >
            <IconButton onClick={() => dispatch(toggleTheme())}>
              {darkMode !== "dark" ? <Brightness7Icon /> : <Brightness4Icon />}
            </IconButton>
            <Link color="black" href="/" underline="hover" sx={{ mx: 2 }}>
              Chi siamo
            </Link>
            <Link color="black" href="/" underline="hover" sx={{ max: 2 }}>
              Dove
            </Link>
            <IconButton color="inherit" onClick={handleSearchClick}>
              <SearchIcon />
            </IconButton>

            <Popover
              open={Boolean(searchAnchor)}
              anchorEl={searchAnchor}
              onClose={handleSearchClose}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "right",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
            >
              <Box sx={{ padding: 2 }}>
                <TextField id="search" placeholder="Cerca..." />
              </Box>
            </Popover>
            <IconButton color="inherit" onClick={handleLoginClick}>
              <Avatar alt="Travis Howard" src="/static/images/avatar/2.jpg" />
            </IconButton>
            <Popover
              open={Boolean(loginAnchor)}
              anchorEl={loginAnchor}
              onClose={handleLoginClose}
              anchorOrigin={{
                vertical: "bottom",
                horizontal: "right",
              }}
              transformOrigin={{
                vertical: "top",
                horizontal: "right",
              }}
            >
              <Box sx={{ padding: 2 }}>
                <form>
                  <TextField
                    id="login"
                    label="login"
                    variant="outlined"
                    fullWidth
                    margin="normal"
                  />
                  <TextField
                    id="password"
                    label="password"
                    type="password"
                    variant="outlined"
                    fullWidth
                    margin="normal"
                  />
                  <Button
                    variant="contained"
                    sx={{ colorBackground: "green" }}
                    type="submit"
                  >
                    Accedi
                  </Button>
                </form>
              </Box>
            </Popover>
          </Box>
          <Drawer open={open} onClose={toggleDrawer(false)}>
            {DrawerList}
          </Drawer>
        </Toolbar>
      </Container>
    </AppBar>
  );
};

export default Header;
