import { light } from "@mui/material/styles/createPalette";
import { createSlice } from "@reduxjs/toolkit";
// importo createSlice

const startTheme = localStorage.getItem("theme") || "light";
// salviamo il tema nel localStorage e se non c'è il tema carica light

const themeSlice = createSlice({
  // qui creaimo il nostro slice di stato con i parametri name per identificare questo Slice
  name: "theme",
  initialState: startTheme,
  // qui prendiamo lo stato iniziale creato sopra con startTheme
  reducers: {
    // Infine il reducer per modificare lo stato una volta avviato
    toggleTheme: (state) => {
      const newTheme = state === "light" ? "dark" : "light";
      // qui con l'operator ternary decidiamo lo stato light e night
      localStorage.setItem("theme", newTheme);
      // e poi mettiamo il nuovo tema nel local storage cosi anche se si aggiorna la pagina
      return newTheme;
    },
    lightTheme: () => {
      localStorage.setItem("theme", "light");
      return "light";
    },
    darkTheme: () => {
      localStorage.setItem("theme", "dark");
      return "dark";
    },
  },
});

export const { toggleTheme, lightTheme, darkTheme } = themeSlice.actions;


export default themeSlice.reducer;
